	import {getLogin,getTask} from '../../service/service.js'
    export default{
    	data(){
    		return{
				show:false,
				listArr:[]
    		}
		},
    	methods:{
    		goDetail(param){
				this.$router.push({path:"/detail",query:{
					data:param
				}})
			}
		},
		created(){
			// var token = localStorage.getItem("token");
			// getTask(token).then((res)=>{
			// 	this.listArr = res.data.tableContent;
			// 	this.$event.$emit("num",res.data.tableContent.length);
				
			// })
			var token = localStorage.getItem("token");
			var that = this;
			setTimeout(function(){
				getTask(token).then((res)=>{
					if(res.status == 200){
						this.listArr = res.data.tableContent;
						this.$event.$emit("num",res.data.tableContent.length);
					}
					else{
						console.log("正常进入");
						var url = location.href;	
						//var url = "https://rfd.roletask.com/Rose/?code=vgHSwrbhwT6noFC54U-2HFh8VnQrxUbwRoIWVPvE0-I&state=1";
						var arr = url.split("?");
						var arr1 = arr[1].split("&")
						var code = arr1[0].split("=")[1];
						getLogin(code).then((result)=>{
							localStorage.setItem("token",result.token);				
						})					
						
					}
				})		

			},0)
			
			
		},
		mounted(){ 	
			var self = this;
			self.mescroll = new MeScroll("td",{
				up:{
					isBounce:false,
					use:false
															
				},
				down:{
					use:false
				}

			})
		}
		
			
    }



