
import { getListTask,getDetailInfo} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

export default{
data(){
return{
infoList:[],
detailInfo:{},
showDialog:false,
show:false,
code:this.$route.query.data

}
},
components: {
Group,
Cell,
Datetime
},
methods:{
agree(){
this.showDialog = true;

},
reject(){
this.showDialog = true;
},
change(value){
console.log(value);
},
geData(code,time,token){
getListTask(code,token).then((result)=>{
this.infoList = result;
console.log(result);

})
getDetailInfo(time,token,code).then((result)=>{
if(result.length>=1){
this.detailInfo = JSON.parse(result[0].json_data);
}
var t = JSON.parse(result[0].json_data);
console.log(t);
})

}


},
created(){
let token  = localStorage.getItem("token");
let time = new Date().getTime();
this.geData(this.code,time,token);
},
mounted(){
var self = this;
self.mescroll = new MeScroll("mescroll",{
up:{
isBounce:false,
use:false
},
down:{
use:false
}
})
},
watch:{
"$route":function(to,from){
this.infoList = [];
this.detailInfo = {};
if(this.$route.path=="/detail"){
let code1 = this.$route.query.data;
console.log(code1);
let token1  = localStorage.getItem("token");
let time1 = new Date().getTime();
this.geData(code1,time1,token1);
}
}
}

}




